.reviews-container {
  max-width: 1200px;
  padding-top: 60px;
  padding-bottom: 60px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  @media screen and (min-width: 768px) {
    width: 768px;
  }
     
  @media screen and (min-width: 1200px) {
    width: 1200px;
  }

}

.slider {  
  position: relative;
  margin: auto;
  min-height: 300px; 
  margin-bottom: 15px;
}



.slider .prev, .slider .next {  
  cursor: pointer;
  position: absolute;
  top: 0;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  color: black;
  font-weight: bold;
  font-size: 18px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
}

.slider .next {
  right: 0;
    border-radius: 3px 0 0 3px;
}

.slider-dots {
  text-align: center;
}

.slider-dots_item {
  cursor: pointer;
  height: 12px;
  width: 12px;
  margin: 0 2px;
  background-color: rgb(151, 117, 148);
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}

.active,
.slider-dots_item:hover {
  background-color: rgb(184, 84, 170);
}

.slider .reviews-item {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s;
}


@-webkit-keyframes fade {
  from {
    opacity: .4
  }
  to {
    opacity: 1
  }
}

@keyframes fade {
  from {
    opacity: .4
  }
  to {
    opacity: 1
  }
  
}


.reviews-item_foto {
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  margin-bottom: 30px;
}

.reviews-item_text {
  
  position: relative;
  margin-left: auto;
  margin-right: auto;
  padding-left: 50px;
  padding-right: 50px;
margin-bottom: 30px;
}

.reviews-item_text::after {  
  content: ""; 
  margin-left: 50%;
  width: 32px;
  height: 4px;  
  background-size: contain;
  display: inline-block;
 background-image: url(../images/reviews/ellipse.png), url(../images/reviews/ellipse.png), url(../images/reviews/ellipse.png); 
 background-size: 4px, 4px, 4px;
  background-repeat: no-repeat;
  background-position: top 0 right 0, top 0 left 0, top 0 center;
   
}

.reviews-item_text-desc::before {
  content: "";
  margin-top: auto; 
  justify-content: center;
  text-align: center;
    width: 46px;
  height: 34px;
  background-size: contain;
  display: inline-block;
 background-image: url(../images/reviews/â€œ.png);
 

}


